import React, { Component, createRef } from 'react'
import PropTypes from "prop-types";


export default class TodoInput extends Component {
    static propTypes = {
        btnText:PropTypes.string
    }
    static defaultProps = {
        btnText:'Ê∑ªÂä†todo'
    }
    
    constructor (){
        super()
        this.state = {
            inputValue:'',
        }
        this.inputDom = createRef()
        // this.addTodoCopy = this.addTodo.bind(this,124)
    }
    
    addTodo  = () =>{
        if(this.state.inputValue.trim() === ''){
            return
        }
        this.props.addTodo(this.state.inputValue)
        this.setState({
            inputValue:''
        },()=>{
            this.inputDom.current.focus()
        })
    }
    //Êê≠ÈÖçbind()ÊîπÂèòthisÊåáÂêë‰ΩøÁî®
    // handleInput(id){
    //   console.log(id)
    // }
    handleInput = (event) =>{
        // console.log(event)
        this.setState({
            inputValue:event.currentTarget.value
        })
        // this.state.inputValue = event.currentTarget.value
    }
    handleKeyUp = (e) =>{
        // console.log(e)
        if(e.keyCode === 13){
            this.addTodo()
        }
    }
    render() {
        // console.log(this.props)
        return (
            <>
                <input 
                type="text" 
                placeholder="ËØ∑ËæìÂÖ•" 
                value={this.state.inputValue}
                onChange={this.handleInput}
                onKeyUp = {this.handleKeyUp}
                ref = {this.inputDom}
                />
                <button onClick={this.addTodo}>
                    {this.props.btnText}
                </button>
                {/* ‰∏ãÈù¢ÁöÑÂÜôÊ≥ïÂèØ‰ª•ÊîπÂèòthisÊåáÂêë,Âê¶ÂàôÂÆö‰πâÁöÑÊñπÊ≥ïÂêçÂ≠óÂøÖÈ°ªÊòØÁÆ≠Â§¥ÂáΩÊï∞Ôºå‰∏çÁÑ∂ÂèëÁîüthisÊåáÂêëÈóÆÈ¢ò(‰∏çÊé®Ëçê) */}
                {/* <button onClick={this.addTodoCopy}>
                    {this.props.btnText}
                </button> */}
            </>
        )
    }
}
