import React, { Component, Fragment } from 'react'
import { 
    TodoHeader,
    TodoList,
    Todoinput,
    Like
 } from "./components";

export default class App extends Component {
    constructor(){
        super()
        this.state = {
            title:'ÂæÖÂäû‰∫ãÈ°πÂàóË°®',
            desc:'‰ªäÊó•‰∫ãÔºå‰ªäÊó•ÊØï',
            todos:[
                {
                    id:1,
                    title:'ÂêÉÈ•≠',
                    isCompleted:true
                },
                {
                    id:3,
                    title:'Áù°Ëßâ',
                    isCompleted:false
                },
                {
                    id:8,
                    title:'ÊâØÊ∑°',
                    isCompleted:false
                }
            ]
        }
    }
    addTodo = (todotitle) =>{
        // console.log(this.setId())
        this.setState({
            todos:this.state.todos.concat({
                id:this.setId(),
                title:todotitle,
                isCompleted:false
            })
        })
    }
    setId = () =>{
        const arr = [...this.state.todos].sort((a,b)=>{
            return a.id-b.id
        })
        let id = 0
        if(arr.length === 0){
            return 0
        }
        if(arr.length === 1){
            return arr[0].id === 0?1:0
        }
        for (let i = 0; i < arr.length; i++) {
            if(i===arr.length-1){
                break
            }else if(arr[i+1].id - arr[i].id > 1){
                id = arr[i].id + 1
                break
            }else{
                id = arr[arr.length - 1].id + 1
            }           
        }
        return id
    }
    todoChange = (todo) =>{
        let newTodo = [...this.state.todos]
        newTodo.forEach((item)=>{
            if(item.id === todo.id){
                item.isCompleted = !item.isCompleted
            } 
        })
        this.setState({
            todos:newTodo
        })
        this.setState((prevState)=>{
            console.log(prevState)
        })
        // console.log(newTodo)
    }
    todoDelete = (todo) =>{
        let newTodo = [...this.state.todos]
        newTodo.forEach((item,index)=>{
            if(item.id === todo.id){
                newTodo.splice(index,1)
            } 
        })
        this.setState({
            todos:newTodo
        })
    }
    render() {
        return (
            <Fragment>
                <TodoHeader desc={this.state.desc}>
                    {this.state.title}
                </TodoHeader>
                <Todoinput addTodo={this.addTodo}></Todoinput>
                <TodoList todos={this.state.todos} todoChange={this.todoChange} todoDelete={this.todoDelete}></TodoList>
                <Like></Like>
            </Fragment>
        )
    }
}
